import React, { useContext } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import ListSubheader from '@material-ui/core/ListSubheader';
import Switch from '@material-ui/core/Switch';
import Button from '@material-ui/core/Button';

import DriveEtaIcon from '@material-ui/icons/DriveEta';
import HomeWorkIcon from '@material-ui/icons/HomeWork';
import AccessibilityNewIcon from '@material-ui/icons/AccessibilityNew';

import AssureContext from '../context/assure-context';


const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
        maxWidth: 360,
        backgroundColor: theme.palette.background.paper,
    },
}));

export default function InsuranceTypeList() {
    const classes = useStyles();
    // const [checked, setChecked] = React.useState(['auto', 'housing']);
    const context = useContext(AssureContext);

    const handleFilter = () => {
        context.addSearchResult();
    }

    const handleToggle = (value) => () => {
        const currentIndex = context.checkedTypes.indexOf(value);
        const newChecked = [...context.checkedTypes];

        if (currentIndex === -1) {
            newChecked.push(value);
        } else {
            newChecked.splice(currentIndex, 1);
        }

        context.setCheckedTypes(newChecked);
    };

    return (
        <List subheader={<ListSubheader>Insurance Type</ListSubheader>} className={classes.root}>
            <ListItem>
                <ListItemIcon>
                    <DriveEtaIcon />
                </ListItemIcon>
                <ListItemText id="switch-list-label-auto" primary="Auto" />
                <ListItemSecondaryAction>
                    <Switch
                        edge="end"
                        onChange={handleToggle('auto')}
                        checked={context.checkedTypes.indexOf('auto') !== -1}
                        inputProps={{ 'aria-labelledby': 'switch-list-label-auto' }}
                    />
                </ListItemSecondaryAction>
            </ListItem>
            <ListItem>
                <ListItemIcon>
                    <HomeWorkIcon />
                </ListItemIcon>
                <ListItemText id="switch-list-label-housing" primary="Housing" />
                <ListItemSecondaryAction>
                    <Switch
                        edge="end"
                        onChange={handleToggle('housing')}
                        checked={context.checkedTypes.indexOf('housing') !== -1}
                        inputProps={{ 'aria-labelledby': 'switch-list-label-housing' }}
                    />
                </ListItemSecondaryAction>
            </ListItem>
            <ListItem>
                <ListItemIcon>
                    <AccessibilityNewIcon />
                </ListItemIcon>
                <ListItemText id="switch-list-label-person" primary="Person" />
                <ListItemSecondaryAction>
                    <Switch
                        edge="end"
                        onChange={handleToggle('person')}
                        checked={context.checkedTypes.indexOf('person') !== -1}
                        inputProps={{ 'aria-labelledby': 'switch-list-label-person' }}
                    />
                </ListItemSecondaryAction>
            </ListItem>
            <ListItem>
                <Button variant="contained" color="primary" disableElevation onClick={handleFilter}>
                    Filter
                </Button>
            </ListItem>
        </List>
    );
}
