import React, { useState, useEffect } from 'react';

import AssureContext from './assure-context';

import ListHilights from '../data/ListHilights.js';
import ListResults from '../data/ListResults.js';

const GlobalState = (props) => {
    /**
     * @dev State variables
     */
    const [results, setResults] = useState(ListResults);
    const [hilights, setHilights] = useState(ListHilights);
    const [searchText, setSearchText] = useState('');
    const [priceRange, setPriceRange] = useState([0, 10000000]);
    const [checkedTypes, setCheckedTypes] = useState(['auto', 'housing', 'person']);

    /**
     * @dev UseEffect
     */

    useEffect(() => {

    }, []);

    /**
     * @dev addSearchResult
     */

    const addSearchResult = async () => {

        if (searchText === "") {
            setResults(results => {
                const list = ListResults.filter((result) => parseInt(result.price) >= priceRange[0] * 1000
                                                        &&  parseInt(result.price) <= priceRange[1] * 1000
                                                        && checkedTypes.includes(result.insuranceType)
                                                        );
                    return list;
            });
        }
        else {
            setResults(results => {
                let text = searchText.toLowerCase();
                const list = ListResults.filter((result) => result.company.toLowerCase().indexOf(text) > -1
                    && parseInt(result.price) >= priceRange[0] * 1000
                    && parseInt(result.price) <= priceRange[1] * 1000
                    && checkedTypes.includes(result.insuranceType)
                    );
                    // && console.log(parseInt(result.price), priceRange[0], priceRange[1]));
                    return list;
            });            
        }//else

    }

    /**
     * Render function
     */

    return (
        <AssureContext.Provider value={{
            results,
            setResults,
            hilights,
            setHilights,
            addSearchResult,
            searchText,
            setSearchText,
            priceRange, 
            setPriceRange,
            checkedTypes, 
            setCheckedTypes,
        }}>
            {props.children}

        </AssureContext.Provider>
    );
}

export default GlobalState;